import {
  FC, HTMLAttributes, ReactNode,
  MouseEvent, useCallback, useState
} from 'react'

import { range } from '@utils/numbers'
import { cn } from '@utils/ui'
import Dropdown from '../'
import useStyles from './styles'

export interface AddItemButtonProps {
  title: ReactNode
  onClick: (evt: MouseEvent<HTMLElement>) => void
}

const AddItemButton: FC<AddItemButtonProps> = ({ title, onClick }) => {
  return (
    <li
      className='cf-dropdown-add-item MuiAutocomplete-option'
      role='option'
      onClick={onClick}
    >
      {title}
    </li>
  )
}

const CustomListbox: FC<HTMLAttributes<HTMLElement>> = (props) => {
  const styles = useStyles()
  const { children, ...rest } = props
  const className = cn(rest.className, styles.root)

  const onClick = useCallback((evt) => {
    // evt.stopPropagation() // Uncomment to disable auto-closing of list
    console.info('onClick :: ', evt)
  }, [])

  return (
    <div {...rest} className={className}>
      <AddItemButton title='+ Add Vendor' onClick={onClick} />
      <AddItemButton title='+ Add Customer' onClick={onClick} />

      {children}
    </div>
  )
}

export interface ListItem {
  id: number
  name: string
}

export const CustomListboxDemo: FC = () => {
  const [value, setValue] = useState<ListItem | null>(null)
  const options = range(1, 20).map(num => ({ id: num, name: `Item #${num}` }))

  return (
    <div>
      <div style={{ width: '180px', marginBottom: 30 }}>
        <Dropdown
          id='custom-listbox-demo'
          onChange={(_, val) => setValue(val)}
          value={value}
          label='Item #'
          options={options}
          ListboxComponent={CustomListbox}
          NoOptionsComponent={CustomListbox}
          isExtendedListbox
        />
      </div>
      <div style={{ width: '220px', marginBottom: 30 }}>
        <Dropdown
          id='custom-listbox-demo'
          onChange={(_, val) => setValue(val)}
          value={value}
          label='Item #'
          options={options}
          ListboxComponent={CustomListbox}
          NoOptionsComponent={CustomListbox}
          isExtendedListbox
        />
      </div>
      <div style={{ width: '380px', marginBottom: 30 }}>
        <Dropdown
          id='custom-listbox-demo'
          onChange={(_, val) => setValue(val)}
          value={value}
          label='Item #'
          options={options}
          ListboxComponent={CustomListbox}
          NoOptionsComponent={CustomListbox}
          isExtendedListbox
        />
      </div>
      <div style={{ width: '500px', marginBottom: 30 }}>
        <Dropdown
          id='custom-listbox-demo'
          onChange={(_, val) => setValue(val)}
          value={value}
          label='Item #'
          options={options}
          ListboxComponent={CustomListbox}
          NoOptionsComponent={CustomListbox}
          isExtendedListbox
        />
      </div>
    </div>

  )
}
